<template>
  <div class="welcome">
    <input type="text" id="keycode" @keydown="keyup_submit(event)">
  </div>
</template>

<script>
export default {

  data () {
    return {
      msg: 'Welcome',
      keycode: '',
    }
  },
  methods:{
    keyup_submit (e) {
      var evt = window.event || e; 
      if (evt.keyCode == 13){
        this.axios.get('http://961719.ichengyun.net/').then((res)=>{
          alert(res.data);
        })
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.welcome{
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  background: url("../assets/bj1.jpg");
  background-repeat: no-repeat;

}
#keycode{
  position: absolute;
  background: transparent;
  height: 50px;
  width: 200px;
  border:0px;
  border-bottom: 5px solid rgba(7, 172, 238, 0.8);
  outline: none;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: rgb(252, 186, 4);
  font-size: 3em;
}
</style>



